
:root {
  --main-bg-color: #6893e9; 
  --main-txt-color:  #33518b;
}

#wrapper {
  background-color: var(--main-bg-color);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;  
  overflow: hidden;
}

#quote-box{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: var(--main-txt-color);
  border: 2px solid var(--main-bg-color);
  border-radius: 13%;
  min-width: 300px;
  min-height: 400px;
  margin: 10em;
  text-align: center; 
  color: var(--main-bg-color);
}

#author {
  font-size: 3em;
  font-family: "Oxygen", sans-serif;
  text-shadow: 2px 2px rgb(1, 1, 46);
}

#text {
  font-size: 2.5em;
  font-family: "Fira Sans", sans-serif;
  margin: 0px 20px;
}

#buttons {
  display: flex;
  justify-content: space-around;
  margin-bottom: 1em;
}
button {
  background-color:var(--main-bg-color); 
  color: var(--main-txt-color);
  border: 2px solid var(--main-bg-color);
  border-radius: 15%;
  font-size: 2em;
  
  -webkit-transition-duration: 0.6s; /* Safari */
  transition-duration: 0.6s;
  cursor: pointer;
  outline: 0;
}

#new-quote {
  transition: all 0.5s;
}
#new-quote span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

#loading {
  font-size: 4em;
  color: var(--main-txt-color);
}
@media screen and (max-width :800px) {
  #quote-box{
    margin: 5em;
  }
}
@media screen and (max-width :450px) {
  #quote-box{
    margin: 1em;
  }
  #author {
    font-size: 2em;
  }
  #text {
    font-size: 1.5em;
    margin: 0px 2px;
  }
}
@media screen and (max-height :450px) {
  #quote-box{
    margin: 1em;
    min-width: 70%;
    min-height: 70%;
  }
  #author {
    font-size: 2em;
  }
  #text {
    font-size: 1.5em;
    margin: 0px 2px;
  }
}
